@import "../../../global/variables";

.active_menu_item {
  color: $active-menu-item;
}

.item_card {
  transition: .5s;
  width: 300px;
  display: flex;
  max-height: 500px;
  flex-direction: column;
  background-color: $item-card-background;
  padding: 20px 32px;
  margin: 4px;

  &:hover {
    cursor: pointer;
    box-shadow: 0 0 5px 1px #e3e3e3;
  }

  &_image {
    align-self: center;
    width: fit-content;
    max-height: 240px;
  }

  &_favorite {
    transition: 0.5s;
    font-size: 22px;
    color: #D3D3D3;

    &:active {
      transition: .1s;
      color: #FF4545;
    }
  }

  &_wrapper {
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }

  &_availability {
    font-size: 12px;

    &_content {
      display: flex;
      flex-direction: row;
      align-items: center;

      img {
        margin: 1px 4px 0 0;
      }
    }
  }

  &_cart {
    cursor: pointer;
    display: flex;
    flex-direction: row;
    align-items: center;
    font-size: 10px;
    color: $white;
    background: linear-gradient(271.38deg, #0B3FC5 -8.46%, #0681E4 75.71%);
    padding: 4px 8px;
    border-radius: 10px;
    transition: .3s;

    &:hover {
      opacity: 0.8;
    }
  }

  &_content {
    min-height: 140px;
    display: flex;
    flex-direction: column;
    padding: 12px 16px;
    margin-top: 40px;
    background-color: $white;
    border-radius: 20px;

    &_more {
      cursor: pointer;
      font-size: 10px;
      width: 90px;
      padding: 8px;
      text-align: center;
      color: $white;
      border-radius: 10px;
      background: linear-gradient(271.38deg, #0B3FC5 -8.46%, #0681E4 75.71%);
    }

    &_status {
      display: flex;
      flex-direction: row;
      justify-content: space-between;
      align-items: center;
    }

    &_title {
      min-height: 30px;
      margin: 12px 0;
      font-size: 14px;
    }

    &_price {
      color: $text-blue;
      font-weight: 500;
    }
  }

  &_favorite {
    margin-bottom: 20px;
    text-align: right;
  }
}