@import "../../../global/variables";

@keyframes animationEasy {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

.popular-items {
  margin-top: 40px;
  width: 1200px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  &_title {
    font-size: 24px;
    font-weight: 700;
  }

  &_menu {
    color: $text-secondary;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;

    &_item {
      cursor: pointer;
      font-size: 12px;
      text-transform: uppercase;
      padding: 0 12px;
    }
  }

  &_content {
    animation: animationEasy 0.3s ease-in-out;
    margin-top: 40px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
  }
}