@import '../../../global/variables';

.header_wrapper_navigation_item.active {
  font-weight: 500;
  color: $white;
}

.header {
  z-index: 10;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 68px;
  display: flex;
  justify-content: center;
  color: rgb(255, 255, 255);
  background-color: $primary-blue;
  box-shadow: 0 4px 23px 0 #00000040;

  &_searched-items {
    right: 0;
    display: flex;
    padding: 0 12px;
    flex-direction: column;
    color: #000000;
    width: 380px;
    background-color: #ffffff;
    border-radius: 20px;
    top: 60px;
    position: absolute;
    box-shadow: 0 4px 23px 0 #00000040;


    &_item {
      position: relative;
      cursor: pointer;
      display: flex;
      flex-direction: row;
      align-self: center;
      padding: 12px 0;
      margin: 0 !important;
      height: auto;
      width: 100%;
      border-top: 1px solid #9C9C9C;

      &:hover {
        .header_searched-items_item_about {
          color: #305878;
        }
      }

      &_about {
        transition: 0.3s;
        font-size: 12px;
        font-weight: 500;
        position: absolute;
        bottom: 16px;
        right: 12px;
      }

      &_text {
        font-size: 14px !important;
        margin-left: 12px;
      }

      &_img {
        display: flex;
        justify-content: center;
        align-items: center;
        height: 76px;
        min-width: 80px;
        width: fit-content;
      }

      img {
        display: flex;
        justify-content: center;
        width: fit-content;
        height: 100%;
      }

      &:first-child {
        border: none;
      }
    }
  }

  img.header_wrapper_control-panel_search_icon {
    right: 8px;
    top: 50%;
    transform: translateY(-50%);
    position: absolute;
    height: 16px;
    width: 16px;
  }

  &_wrapper {
    position: relative;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    width: 1200px;
    height: 100%;
    margin: 0 60px;

    &_logo {
      cursor: pointer;
      width: 25%;
      display: flex;
      flex-direction: row;
      justify-content: flex-start;
      align-items: center;

      img {
        padding-top: 4px;
        height: 42px;
      }

      span {
        padding: 0 4px;
        font-size: 24px;
        text-transform: uppercase;
      }
    }

    &_navigation {
      width: 50%;
      display: flex;
      flex-direction: row;
      justify-content: center;
      align-items: center;
      font-size: 18px;

      &_item {
        color: rgba(255, 255, 255, 0.6);
        padding: 0 18px;
      }
    }

    &_control-panel {
      width: 25%;
      display: flex;
      flex-direction: row;
      justify-content: space-between;
      align-items: center;

      div {
        cursor: pointer;
        margin: 0 8px;
      }

      div:not(:first-child) {
        font-size: 24px;
      }

      &_search {
        position: relative;
        width: 280px;
        height: 32px;

        input {
          padding: 0 12px;
          outline: none;
          border: none;
          border-radius: 15px;
          width: 100%;
          height: 100%;
        }
      }
    }
  }
}