.catalog {
  margin-top: 40px;
  display: flex;
  justify-content: space-between;
  flex-direction: column;
  width: 1200px;

  &_wrapper {
    display: flex;
    justify-content: center;
    width: 100%;
  }

  &_title {
    margin: 20px 0 60px;
    width: 100%;
    text-align: center;
    font-size: 24px;
    font-weight: 700;
  }

  &_content {
    width: 1200px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: flex-start;
  }
}