@import "src/global/variables";

.swiper-wrapper {
  padding-right: 16%;
  display: flex;
  align-items: center;
}

.swiper-slide-next {
  width: 50% !important;
}

.swiper-slide-next .slider_slide {
  font-size: 16px;
}

.swiper-slide-next .slider_text {
  text-align: left;
  margin: 60px 0;
}

.swiper {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.slider {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;

  &_slide {
    font-size: 9px;
    width: 100%;
    height: 100%;
    color: $white;
    padding: 72px 100px;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    background-position: 0;
    background-repeat: no-repeat;
    background-size: 100%;
    border-radius: 16px;

    margin: 28px 0;

    text-align: center;
    display: -webkit-box;
    display: -ms-flexbox;
    display: -webkit-flex;
    display: flex;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    -webkit-justify-content: center;
    -webkit-box-align: center;
    -ms-flex-align: center;
    -webkit-align-items: center;


    &:hover {
      transition: .3s;
      cursor: pointer;
    }

    img {
      width: 50%;
    }
  }

  &_title {
    text-align: left;
    font-size: 3.5em;
    font-weight: 700;
  }

  &_text {
    text-align: left;
    margin: 20px 0;
    font-size: 1.25em;
  }

  &_btn {
    font-size: 1em;
    border-radius: 30px;
    padding: 12px 24px;
    width: fit-content;
    background: $btn-color;
    transition: .3s;
    box-shadow: 0 0 29px 11px rgba(52, 100, 223, 0.64);

    &:hover {
      background: rgba(22, 83, 231, 0.82);
    }
  }
}