.item-page {
  display: flex;
  justify-content: center;
  align-items: center;

  &_compare {
    outline: none;
    position: absolute;
    top: -24px;
    right: 0;
    font-size: 14px;
    font-weight: 500;
    height: 32px;
    line-height: 24px;
    background-color: #305878;
    color: #ffffff;
    text-align: center;
    border-radius: 20px;
    width: 240px;
    transition: .3s;

    &:hover {
      cursor: pointer;
      opacity: .8;
    }
  }

  &_price {
    font-size: 20px;
    color: #305878;
    padding: 20px 4px 12px;
  }

  &_add-to-cart {
    margin-top: 20px;
    text-align: center;
    line-height: 32px;
    border-radius: 20px;
    height: 32px;
    color: #FFFFFF;
    width: 240px;
    background: linear-gradient(271.38deg, #0B3FC5 -8.46%, #0681E4 75.71%);
    transition: .3s;

    &:hover {
      cursor: pointer;
      opacity: 0.8;
    }
  }

  &_image {
    width: 40%;

    img {
      width: 90%;
    }
  }

  &_wrapper {
    position: relative;
    width: 1200px;
    display: flex;
    flex-direction: column;
  }

  &_content {
    margin-top: 40px;
    display: flex;
    flex-direction: row;
  }

  &_configuration {
    margin-top: 20px;
    display: flex;
    flex-direction: column;

    &_group {
      margin: 12px 0;
      display: flex;
      flex-direction: column;

      &_title {
        color: #9C9C9C;
        margin: 12px 0;
      }
    }

    &_items {
      display: flex;
      flex-direction: row;

      div {
        margin: 0 8px;

        &:first-child {
          margin: 0;
          margin-right: 8px;
        }
      }
    }
  }

  &_title {
    font-size: 24px;
    font-weight: 500;
  }

}

.color_config {
  font-size: 0;
  width: 28px;
  height: 28px;
  border-radius: 50px;
}

.config-item-block {
  font-size: 14px;
  font-weight: 500;
  padding: 8px 12px;
  background: rgba(234, 234, 236, 0.55);
  border-radius: 20px;

  &-active {
    color: #FFFFFF;
    background: rgba(9, 96, 212, 0.82);
    box-shadow: 0px 0px 29px 11px rgba(52, 100, 223, 0.26);
    border-radius: 20px;
  }

  &-color-active {
    box-shadow: 0 0 3px 2px rgba(0, 0, 0, 0.5);
  }
}